export default {
	data() {
		return {
			amount:0,
			orderName:'',
			paytype:'alipay'//支付类型
		};
	},
	onLoad(e) {
		this.amount = parseFloat(e.amount).toFixed(2);
		uni.getStorage({
			key:'paymentOrder',
			success: (e) => {
				if(e.data.length>1){
					this.orderName = '多商品合并支付'
				}else{
					this.orderName = e.data[0].name;
				}
				uni.removeStorage({
					key:'paymentOrder'
				})
			}
		})
	},
	methods:{
		doDeposit(){
			//模板模拟支付，实际应用请调起微信/支付宝
			uni.showLoading({
				title:'支付中...'
			});
			setTimeout(()=>{
				uni.hideLoading();
				uni.showToast({
					title:'支付成功'
				});
				setTimeout(()=>{
					uni.redirectTo({
						url:'../../pay/success/success?amount='+this.amount
					});
				},300);
			},700)
		}
	}
}
